'use strict';
	var _default = {
		music: 'http://musics.gimhoy.com/mp3/4b1ef556f67205cf3d07fd3afdd153cb.mp3',
		eleLeft: '50px',
		eleTop: '50px',
		bg: '../images/audio_1x1.png',
		htmlData: `<audio src="http://musics.gimhoy.com/mp3/4b1ef556f67205cf3d07fd3afdd153cb.mp3" loop="loop" autoplay="autoplay" style="width: 0px;height: 0px;"></audio>`,
		flag: true,
	}
export default {
	miniAudio: function (option) {
		let options = Object.assign({},_default,option);//参数合并

		//浏览器渲染
		let oAudio = document.createElement('div');
		oAudio.id = 'minimalistAudio';
		oAudio.style = ' position: absolute;width: 30px;height: 30px;cursor: pointer;z-index: 99999;'

		oAudio.style.left = options.eleLeft;
		oAudio.style.top = options.eleTop;
		oAudio.style.background = 'transparent url('+ options.bg + ') no-repeat';
		oAudio.style.backgroundSize = 'contain';

		oAudio.innerHTML = options.htmlData;

		let parentEle = document.getElementById(option.eleId);
		if (!parentEle) parentEle = document.body;
		parentEle.appendChild(oAudio);

		var audioDom = oAudio.getElementsByTagName('audio')[0];
		audioDom.src = options.music;

		var _device = /Android|iPhone|iPad|iPod|BlackBerry|webOS|Windows Phone|SymbianOS|IEMobile|Opera Mini/i.test(navigator.userAgent);//判断设备类型
		var bindEvent = _device? 'touchstart' : 'click';

		oAudio.addEventListener(bindEvent, function(e) {
			options.flag = !options.flag;
			if (options.flag) {
				audioDom.play();
			}
			else {
				audioDom.pause();
			}
		});
	},
}



